.projects > div{
    background: rgba( 0, 0, 0, 0.75 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 14.0px );
    -webkit-backdrop-filter: blur( 14.0px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    padding: 2rem;
    margin-top: 2rem;
    margin-left: 1rem;
    margin-right: 1rem;
    cursor: pointer;
}

.projects::-webkit-scrollbar{
    display: none;
}

.upperProjectText{
    visibility: hidden;
}

